import { StyledWrapper, Title } from './ImageTile.styles';
import propTypes from './ImageTile.types';

/**
 * A simple image tile for displaying an image with a title
 */

const ImageTile = ({ image, title, url }) => {
  return (
    <StyledWrapper href={url}>
      {image}
      <Title>{title}</Title>
    </StyledWrapper>
  );
};

ImageTile.propTypes = propTypes;

export default ImageTile;
