import { styled } from 'styled-components';

import * as device from '#theme/devices';

export const StyledWrapper = styled.div.attrs(({ url }) => ({
  as: url ? 'a' : 'div',
}))`
  display: block;
  position: relative;

  @media ${device.tablet} {
    height: 100%;
  }

  img {
    display: block;
    aspect-ratio: 3 / 2;
    object-fit: cover;
  }
`;

export const Title = styled.div`
  position: relative;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 8px;
  background-color: ${({ theme }) => theme.color.dark.canvas.base};
  color: ${({ theme }) => theme.color.dark.ink.base};
  font: ${({ theme }) => theme.textStyle.ImageTile.title};

  @media ${device.tablet} {
    position: absolute;
  }

  @media ${device.laptop} {
    padding: 16px;
  }
`;
